import React, { useEffect, useState } from 'react';
import './Gallery.css';
import 'antd/dist/antd.css';
import { Image } from 'antd';
 
 
const Images = [
    { id: '1', imageName: 'img_(1).jpg' ,  tag:  'jpg'},
    { id: '2', imageName: 'img_(2).jpg' ,  tag:  'jpg'},
    { id: '3', imageName: 'img_(3).jpg' ,  tag:  'jpg'},
    { id: '4', imageName: 'img_(4).jpg' ,  tag:  'jpg'},
    { id: '5', imageName: 'img_(5).jpg' ,  tag:  'jpg'},
    { id: '6', imageName: 'img_(6).jpg' ,  tag:  'jpg'},
    { id: '7', imageName: 'img_(7).jpg' ,  tag:  'jpg'},
    { id: '8', imageName: 'img_(1).png' ,  tag:  'png'},
    { id: '9', imageName: 'img_(2).png' ,  tag:  'png'},
    { id: '10', imageName: 'img_(3).png' ,  tag:  'png'},
    { id: '11', imageName: 'img_(4).png' ,  tag:  'png'},
    { id: '12', imageName: 'img_(5).png' ,  tag:  'png'},
];

export default function Gallery() {

    const[tag, setTag] = useState('all');
    const[filterImage, setFilterImage] = useState([]);

    useEffect( ()=> {
      tag === 'all' ? setFilterImage(Images) : setFilterImage(Images.filter(image => image.tag === tag))
    },[tag])

  return (
    <>
        <div>
            <div className='tags'>
            <TagButton name='all' handelSetTag={setTag} tagActive={tag === 'all' ? true : false }/>
            <TagButton name='jpg' handelSetTag={setTag} tagActive={tag === 'jpg' ? true : false }/>
            <TagButton name='png' handelSetTag={setTag} tagActive={tag === 'png' ? true : false }/>

            </div>
            <Image.PreviewGroup>
            <div className='container'>
                {
                    filterImage.map(image => (
                    <div key={image.id} className='image-card'>
                        <Image className='photo' src={`/images/${image.imageName}`} alt=""/>
                    </div>                    
                ))}
            </div> 
            </Image.PreviewGroup>
        </div>
    </>
  )
}

const TagButton = ({name , handelSetTag , tagActive}) => {
    return <button className={`tag ${tagActive ? 'active' : null }`} onClick={ ()=>handelSetTag(name) } >{ name.toUpperCase() }</button>;
}

