import React ,{useState} from 'react'

export default function RandomImage() {

  const [result, setResult] = useState({});

  function onClick() {
    fetch("https://mocki.io/v1/73b0311c-5aa7-4448-8db7-15361f28efd3")
      .then(res => res.json())
      .then(
        (result) => {
          setResult(result);
        }
      )
  }
  
  

  let compo ;
  if (result.profile_pic) {
    compo = <div> <img src={result.profile_pic} /> <p>{result.name}</p></div>
  } else {
    compo = <button onClick={onClick}> Change Content </button>
  }

  return (
      <>
      <div >
      <h1>The XMLHttpRequest Object</h1>
        {compo}
          
        
        </div>
      </>
              
      )
}
